import React, { useEffect, useRef } from "react";

export default function RainBackground({ mode }) {
  const canvasRef = useRef();
  const audioRef = useRef();

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    let width = (canvas.width = window.innerWidth);
    let height = (canvas.height = window.innerHeight);

    const drops = Array.from({ length: 200 }, () => ({
      x: Math.random() * width,
      y: Math.random() * height,
      length: Math.random() * 20 + 10,
      speed: Math.random() * 4 + 2,
    }));

    function animate() {
      ctx.clearRect(0, 0, width, height);
      ctx.strokeStyle = mode === "dark" ? "rgba(255,255,255,0.3)" : "rgba(100,100,255,0.4)";
      ctx.lineWidth = 2;
      drops.forEach((d) => {
        ctx.beginPath();
        ctx.moveTo(d.x, d.y);
        ctx.lineTo(d.x, d.y + d.length);
        ctx.stroke();
        d.y += d.speed;
        if (d.y > height) d.y = -d.length;
      });
      requestAnimationFrame(animate);
    }
    animate();

    const handleResize = () => {
      width = canvas.width = window.innerWidth;
      height = canvas.height = window.innerHeight;
    };
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [mode]);

  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = mode === "dark" ? 0.5 : 0.3; // حجم صدا
      audioRef.current.play().catch(() => {}); // شروع خودکار
    }
  }, [mode]);

  return (
    <>
      <canvas
        ref={canvasRef}
        style={{
          position: "fixed",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          zIndex: -1,
          pointerEvents: "none",
        }}
      />
      <audio ref={audioRef} src="/sounds/rain.mp3" loop />
    </>
  );
}
